<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
            <a-alert
                    message="功能提示"
                    description="党员可以浏览的所有党支部发表（已发布且已通过审核）的新闻，只可以浏览，无编辑、删除权限。"
                    type="info"
                    showIcon
            />
        <div style="height: 10px"></div>
        <CategoryList :id="categoryId"
                      :request="request"
                      :columns="columns"
                      @handleOperate="handleOperate" ref="list"/>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import CategoryList from '@/components/category/list/Index'
    import {deleteArticle} from '@/api/article'
    import {getPublishedArticleList, getAuditUserArticleList, getArticleList} from '@/api/article'

    export default {
        components: {
            TopButtons, CategoryList
        },
        data() {
            return {
                categoryId: 2,
                request: getPublishedArticleList,
                columns: [
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                        }
                    }
                ],
            }
        },
        methods: {
            handleOperate(type, row) {
                let id = row.id;
                switch (type) {
                    case 'check':
                        this.$router.push({name: '新闻查看', params: {id: id}})
                        break;
                    case 'edit':
                        this.$router.push({name: '新闻编辑', params: {id: id}})
                        break;
                    case 'delete':
                        deleteArticle(id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.list.reFetch();
                            });
                        break;

                }
            },
        }
    }
</script>
